.payment-page{max-width:1000px;margin:0 auto;padding:2rem}.payment-page .payment-container{background:#fff;border-radius:15px;box-shadow:0 5px 15px rgba(0,0,0,.1);padding:2rem}.payment-page .payment-container h1{color:#2c3e50;margin-bottom:2rem;text-align:center}.payment-page .payment-container h2{color:#34495e;margin-bottom:1.5rem}.payment-page .plan-summary{background:#f8f9fa;border-radius:10px;padding:1.5rem;margin-bottom:2rem}.payment-page .plan-summary .plan-details{display:flex;justify-content:space-between;align-items:center;margin-bottom:1rem}.payment-page .plan-summary .plan-details .plan-name{font-size:1.2rem;font-weight:600;color:#2c3e50}.payment-page .plan-summary .plan-details .plan-price{font-size:1.2rem;font-weight:600;color:#3498db}.payment-page .plan-summary .plan-features ul{list-style-type:none;padding:0;margin:0}.payment-page .plan-summary .plan-features ul li{padding:.5rem 0;position:relative;padding-left:1.5rem;color:#34495e}.payment-page .plan-summary .plan-features ul li:before{content:"";color:#2ecc71;position:absolute;left:0;font-weight:bold}.payment-page .payment-methods .payment-method-tabs{display:flex;margin-bottom:1.5rem;border-bottom:1px solid #e0e0e0}.payment-page .payment-methods .payment-method-tabs .method-tab{padding:.8rem 1.5rem;background:none;border:none;border-bottom:3px solid rgba(0,0,0,0);font-size:1rem;color:#7f8c8d;cursor:pointer;transition:all .3s ease}.payment-page .payment-methods .payment-method-tabs .method-tab:hover{color:#3498db}.payment-page .payment-methods .payment-method-tabs .method-tab.active{color:#3498db;border-bottom-color:#3498db;font-weight:600}.payment-page .payment-methods form .form-group{margin-bottom:1.5rem}.payment-page .payment-methods form .form-group label{display:block;margin-bottom:.5rem;color:#34495e;font-weight:500}.payment-page .payment-methods form .form-group input{width:100%;padding:.8rem;border:1px solid #ddd;border-radius:4px;font-size:1rem}.payment-page .payment-methods form .form-group input:focus{outline:none;border-color:#3498db;box-shadow:0 0 0 2px rgba(52,152,219,.2)}.payment-page .payment-methods form .form-group.half{width:48%}.payment-page .payment-methods form .form-row{display:flex;justify-content:space-between}.payment-page .payment-methods form .error-message{color:#e74c3c;margin-bottom:1rem}.payment-page .payment-methods form .form-actions{display:flex;gap:1rem;margin-top:2rem}.payment-page .payment-methods form .form-actions button{padding:.8rem 1.5rem;border:none;border-radius:4px;cursor:pointer;font-size:1rem;transition:all .3s;font-weight:600}.payment-page .payment-methods form .form-actions button.pay-button{background-color:#2ecc71;color:#fff;flex:2}.payment-page .payment-methods form .form-actions button.pay-button:hover:not(:disabled){background-color:#27ae60}.payment-page .payment-methods form .form-actions button.pay-button:disabled{background-color:#bdc3c7;cursor:not-allowed}.payment-page .payment-methods form .form-actions button.cancel-button{background-color:#e74c3c;color:#fff;flex:1}.payment-page .payment-methods form .form-actions button.cancel-button:hover:not(:disabled){background-color:#c0392b}.payment-page .payment-methods form .form-actions button.cancel-button:disabled{background-color:#bdc3c7;cursor:not-allowed}.payment-page .loading{text-align:center;padding:2rem;color:#7f8c8d}